body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  background-color: azure;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.container {
  max-width: 1280px;
  min-width: 300px;
  
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 20px;

  margin: auto;
  padding: 10px;
}
.card {
  max-width: 350px;
  min-width: 250px;
  background-color: white;
  border-radius: 10px;
  position: relative;
  padding-bottom: 10px;
}
.card img {
  width: 100%;
  height: 200px;
  border-top-right-radius: 10px;
  border-top-left-radius: 10px;
}
.card button {
  padding: 6px 12px;
  border-radius: 5px;
  background-color: rgb(48, 27, 240);
  color: white;
  font-size: 21px;
  font-weight: 500;
  outline: none;
  border: none;
}
.div-delete {
  position: absolute;
  top: 10px;
  right: 10px;
  transition: all 1s;
  transition-delay: 2s;
   height: 30px;
   background-color: #fff;
   display: flex;
   justify-content: center;
   align-items: center;
  width: 30px;
  border-radius: 15px;
}
.div-delete:hover {
  background-color: yellow;
 
  cursor: pointer;
 
}

/* This is form Styling */

form{
  border: 1px solid black;
  padding: 10px 10px;
  max-width: 500px;
  min-width: 300px;
  border-radius: 5px;
}

.form-label{
  color: black;
  font-size: 15px;
  cursor: pointer;
  font-weight: 500;
  text-transform: uppercase;
}
.form-input{
  width: 80%;
  padding: 10px 20px;
  outline: none;
  border: none;
  border-radius: 6px;
}
.form-input-submit{
  background-color: black;
  color: #fff;
  border-radius: 8px;
  padding: 10px 20px;
  outline: none;
  border: none;
  margin-top: 10px;
  cursor: pointer;
  transition: all 2s;
}
.form-input-submit:hover{
  background-color: blueviolet;
  color: yellow;
}
